<template>
  <div>
 <header class="p-4 text-white shadow" style="background-color: #155FB4">
    <div class="container">
      <div class="d-flex flex-wrap align-items-center justify-content-center justify-content-lg-start">
        <a href="/vue_bootstrap_spa/" class="d-flex align-items-center mb-2 mb-lg-0 text-white text-decoration-none">
          <img src="https://stc-spb.ru/local/templates/stc/img/header/header_logo.svg" alt="логотип">
        </a>
  <!-- Тут менюшка максимально простая -->
        <ul class="nav col-12 col-lg-auto me-lg-auto mb-2 justify-content-center mb-md-0">
          <li><a href="/vue_bootstrap_spa/" class="nav-link px-3 text-white"> Главная</a></li>
          <li><a href="#" class="nav-link px-3 text-white">О нас</a></li>
          <li><a href="#" class="nav-link px-3 text-white">Услуги</a></li>
        </ul> 
  <!-- Здесь Кабинет с svg -->
          <button @click="showModal" type="button" class="btn btn-outline-light me-3"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-fill" viewBox="0 0 16 16">
          <path d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H3zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
          </svg> Кабинет</button>
        </div>
      </div>
  <!-- Это модальное окно и компонента кабинета пользователя -->
      <my-modal v-model:show="modalVisible">
        <user-cabinet />
        <div class="d-grid gap-2 d-md-flex justify-content-md-end">
          <button @click="hideButt" class="btn btn-outline-primary btn-sm" type="button">Закрыть</button>
        </div>
      </my-modal>
    </header>
  <!-- 
    Здесь форма регистрации с bootstrap стилями 
    Связал с помощью v-model с data, так же применил функцию trim она обрезает пробелы слева и справа
  -->
<main>
  <form @submit="checkForm" class="col-sm-6 mt-5 mx-auto px-4">
    <div class="mb-3">
    <label for="name" class="form-label">Ваше имя</label>
    <input v-model.trim="formReg.name" :class="v$.formReg.name.$error ? 'is-invalid' : ''" type="text" class="form-control mb-1" id="name">
      <p v-if="v$.formReg.name.$invalid && v$.formReg.name.required" class="text-secondary small"> 
        Поле должно содержать минимум 2 символа
      </p>
  </div>
  <div class="mb-3">
    <label for="surname" class="form-label">Ваша фамилия</label>
    <input v-model.trim="formReg.surname" :class="v$.formReg.surname.$error ? 'is-invalid' : ''" type="text" class="form-control mb-1" id="surname">
    <p v-if="v$.formReg.surname.$invalid && v$.formReg.surname.required" class="text-secondary small"> 
        Поле должно содержать минимум 2 символа
      </p>
  </div>
  <div class="mb-3">
    <label for="email" class="form-label">Email</label>
    <input v-model.trim="formReg.email" :class="v$.formReg.email.$error ? 'is-invalid' : ''" type="text" class="form-control mb-1" id="email">
    <p v-if="v$.formReg.email.$invalid && v$.formReg.email.required" class="text-secondary small"> 
        Проверьте правильность Email адреса
      </p>
  </div>
    <div class="mb-3">
      <label for="password" class="form-label">Пароль</label>
      <input v-model.trim="formReg.password.password" :class="v$.formReg.password.password.$error ? 'is-invalid' : ''"  type="password" class="form-control mb-1" id="password">
      <p v-if="v$.formReg.password.password.$invalid && v$.formReg.password.password.required" class="text-secondary small"> 
        Пароль должен содержать минимум 6 символов
      </p>
  </div>
  <div class="mb-3 ">
    <label for="passwordConfirm" class="form-label">Подтверждение пароля</label>
    <input v-model.trim="formReg.password.confirm" :class="v$.formReg.password.confirm.$error ? 'is-invalid' : ''"  type="password" class="form-control mb-1" id="confirmPassword">
    <p v-if="v$.formReg.password.confirm.$invalid && v$.formReg.password.confirm.required" class="text-secondary small"> 
        Пароли должны совпадать
      </p>
  </div>
  <button @click="registerUser" type="button" class="btn btn-primary mt-3 mb-5"> Зарегистрироваться</button>
  </form>
    </main>
  </div>
</template>

<script>
import UserCabinet from '../components/UserCabinet.vue'
import MyModal from '../components/MyModal.vue'
import useValidate from '@vuelidate/core'
import { required, minLength, email, sameAs } from 'vuelidate/lib/validators'

export default {
  components: {
    MyModal, UserCabinet
  },
  data() {
    return { 
      v$: useValidate(),
      modalVisible: false,
      step: 1,
        formReg: {
          name: '',
          surname: '',
          email: '',
          password: {
            password: '',
            confirm: '',
          }
      }
    }
  },
  methods: {
    showModal()  {
      this.modalVisible = true
    },
    hideButt() {
      this.modalVisible = false
    },
    registerUser() {
      this.v$.$validate()
      if (!this.v$.$error) {
        alert('Регистрация прошла успешно');
        this.$router.push('/confirmreg/');
        console.log('Вы зарегистрированы', this.formReg);
      } else {
        alert('Ошибка регистрации');
        this.$router.push('/register');
      }
    }, 
  },
  validations() {
    return  {
      formReg: {
          name: { required, minLength: minLength(2) },
          surname: { required, minLenghth :minLength(2) },
          email: { required, email },
          password: {
            password: { required, minLength: minLength(6) },
            confirm: { required, sameAsPassword: sameAs('password') },
          }
      }
    }
  }
}
document.title = 'Регистрация'
</script>

